package eu.karelhovorka.util.html

import org.w3c.dom.HTMLElement
import kotlin.browser.document
import kotlin.math.roundToInt

class ProgressLoader(
        val element: HTMLElement = document.getElementById("progress") as HTMLElement,
        val bar: HTMLElement = document.getElementById("bar") as HTMLElement,
        val caption: HTMLElement = element.querySelector(".caption") as HTMLElement
) {

    fun start() {
        updateProgress(0.01)
    }

    fun start(text: String) {
        caption.innerText = text
    }


    fun finish() {
        element.hide()
    }

    fun updateProgress(current: Int, max: Int) {
        updateProgress((current + 1).toDouble() / max.toDouble())
    }

    private fun updateProgress(progress: Double) {
        if (progress >= 1.0) {
            finish()
            return
        }
        element.show("block")
        setWidthToValue(progress)
    }

    private fun setWidthToValue(progress: Double) {
        bar.style.width = (progress * 100.0).roundToInt().toString() + "%"
    }

}